body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.app {
  padding-left: 240px;
  box-sizing: border-box;
}
.app .sider {
  position: fixed;
  width: 240px;
  height: 100%;
  top: 0px;
  left: 0px;
  background-color: #f7f7f7;
}
.sample-content {
  padding: 16px 16px;
  position: absolute;
  height: 100%;
  top: 0;
  left: 240px;
  bottom: 0;
  right: 0;
  overflow: auto;
  background-color: #f7f7f7;
}
.app .sider h1 {
  position: relative;
  background-color: #f0f0f0;
  padding: 10px 0 10px 28px;
  height: 80px;
}
.app .sider h1 .header-name {
  display: block;
  font-size: 12px;
  color: #999;
}
.app .sider img.logo {
  position: absolute;
  right: 10px;
  top: 10px;
  width: 32px;
}
.app .sider h1 span.example-title {
  font-size: 20px;
  display: block;
  margin-top: 5px;
  color: #333;
  display: inline-block;
}
.app .sider h1 .ant-select {
  margin-top: 5px;
  color: #666;
  margin-left: 10px;
}
.app .sider .scroll-container::-webkit-scrollbar {
  width: 0.5rem;
  height: 0.5rem;
}
.app .sider .scroll-container::-webkit-scrollbar-thumb {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  border-color: transparent;
  background-color: #ddd;
  z-index: 40;
}
.app .sider .scroll-container::-webkit-scrollbar-corner {
  background-color: rgba(0, 0, 0, 0);
}
.app .sider ul,
.app .sider li {
  margin: 0;
  padding: 0;
  list-style: none;
}
.app .sider li {
  line-height: 260%;
}
.app .sider li a {
  display: block;
  padding-left: 28px;
}
.app .sider li a.active {
  color: #f90;
}
.app .sider .social {
  padding-left: 28px;
  margin-top: 20px;
  line-height: 40px;
}
.app .example-container h1 {
  border-bottom: 1px solid #eee;
  padding-bottom: 15px;
  margin-bottom: 15px;
}
.app .example-container .ant-form {
  width: 600px;
}
.example-description {
  color: #777;
  font-size: 14px;
  margin: 10px 0 0 0;
  line-height: 150%;
}
.code-viewer {
  -webkit-transform: translateY(-16px);
          transform: translateY(-16px);
}
.code-viewer-title {
  margin-top: 8px;
  background-color: #333;
  color: white;
  padding: 4px 12px;
}
body {
  overflow: hidden;
}
.readonly-sample .ant-slider {
  min-width: 180px;
}
.readonly-sample-minimap {
  width: 250px;
  height: 200px;
}
.readonly-sample-edge-label {
  text-align: center;
  margin-top: -20px;
}
.readonly-sample-start-node,
.readonly-sample-end-node,
.readonly-sample-joint-node {
  border: 1px solid black;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  background: white;
}
.readonly-sample-joint-node {
  font-size: 18px;
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
}
.readonly-sample-end-node::before {
  content: '';
  position: absolute;
  border: 1px solid black;
  border-radius: 50%;
  background: black;
  top: 5px;
  left: 5px;
  width: 20px;
  height: 20px;
}
.readonly-sample-connector-in,
.readonly-sample-connector-out {
  width: 16px;
  height: 16px;
  border: 1px solid black;
  border-radius: 50%;
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  background: white;
}
.readonly-sample-connector-in {
  left: -8px;
}
.readonly-sample-connector-out {
  right: -8px;
}
.readonly-sample-node {
  width: 100%;
  height: 100%;
}
.readonly-sample-node-group-content,
.readonly-sample-node-content {
  position: relative;
  width: 100%;
  height: 100%;
  border: 1px solid #8799c1;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
}
.readonly-sample-node-group-content {
  padding: 4px 20px;
}
.readonly-sample-node-group-content > div {
  height: 30px;
}
.readonly-sample-node-group-content > div a {
  margin-left: 6px;
  font-size: 16px;
}
.readonly-sample-node-content {
  align-items: center;
  justify-content: center;
}
.readonly-sample-content {
  height: 400px;
  position: relative;
}
.readonly-sample-content-nice-dag {
  height: 100%;
}
.readonly-sample-content-minimap {
  position: absolute;
  right: 0;
  width: 250px;
  height: 120px;
  bottom: 20px;
  border: 1px solid gray;
  padding: 2px 2px;
  background-color: #efefef;
}
.readonly-sample-content-minimap-body {
  width: 100%;
  height: 100%;
}
.readonly-sample-minimap-viewbox {
